<template>
    <div>
        <el-row>
            <headers :title='title'></headers>
        </el-row>
        <el-row>
            <el-col :span='18' :offset='3'>
                <div class='modify_pw'>
                    <div class="modify_title">
                        <span>修改密码</span>
                        <span class="r">
                            <span class='rs'>*</span>
                            <span>必填项</span>
                        </span>
                    </div>
                    <el-row>
                        <el-col :span='10' :offset='7'>
                            <div class="ads as">
                                <el-form ref="form" :model="form" label-width="120px">
                                    <el-form-item label="登录账号：" class='is-required'>
                                        <el-input v-model="form.userName" :disabled="true"></el-input>
                                    </el-form-item>
                                </el-form>
                            </div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span='10' :offset='7'>
                            <div class="ads">
                                <el-form ref="form" :model="form" label-width="120px" :rules="rules">
                                    <el-form-item label="新密码：" prop='pass' class='is-required'>
                                        <el-input type="password" v-model="form.pass" placeholder='请填写新的登录密码' autocomplete="off"></el-input>
                                    </el-form-item>
                                    <el-form-item label="确认新密码：" prop='checkPass' class='is-required'>
                                        <el-input type="password" v-model="form.checkPass" placeholder='请重复填写新的登录密码' autocomplete="off"></el-input>
                                    </el-form-item>
                                </el-form>
                            </div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span='6' :offset='9'>
                            <div class='subs'>
                                <el-row>
                                    <el-col :span='12' :offset='6'>
                                        <el-button type="primary" @click='sub("form")'>提交</el-button>
                                    </el-col>
                                    <!-- <el-col :span='12'>
										<el-button icon='el-icon-refresh' @click='reset'>重置</el-button>
									</el-col> -->
                                </el-row>
                            </div>
                        </el-col>
                    </el-row>
                </div>
            </el-col>
        </el-row>
    </div>
</template>
<script>
import headers from '../../components/header.vue'
export default {
    components: {
        headers
    },
    data() {
        var validatePass = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('请输入密码'));
            } else {
                if (this.form.checkPass !== '') {
                    this.$refs.form.validateField('checkPass');
                }
                callback();
            }
        };
        var validatePass2 = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('请再次输入密码'));
            } else if (value !== this.form.pass) {
                callback(new Error('两次输入密码不一致!'));
            } else {
                callback();
            }
        };
        return {
            title: '修改密码',
            form: {
                userName: '',
                pass: '',
                checkPass: ''
            },
            rules: {
                pass: [{
                    validator: validatePass,
                    trigger: 'blur'
                }, {
                    pattern: /^(\w){4,20}$/,
                    message: '只能输入4-20个字母、数字、下划线'
                }],
                checkPass: [{
                    validator: validatePass2,
                    trigger: 'blur'
                }, {
                    pattern: /^(\w){4,20}$/,
                    message: '只能输入4-20个字母、数字、下划线'
                }]
            }
        }
    },
    methods: {
        /*
        _this.$axios.post('/tomlive/user/updateUserPassword', this.$qs.stringify({
        			userName: _this.form.userName,
        			password: _this.form.checkPass
        		}), {
        			headers:{

        			}
        		}).then((res) => {
        			console.log(res)
        		})
        		this.$router.push('/home/sub_password')
        */
        reset() {
            this.form.checkPass = '',
                this.form.pass = ''
        },
        sub(formName) {
            var _this = this
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    // alert('submit!');
                    this.$confirm('确认修改密码, 是否继续?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then((action) => {
                        if (action === 'confirm') {
                            _this.$axios.post('/tomlive/user/updateUserPassword', this.$qs.stringify({
                                userName: _this.form.userName,
                                password: _this.form.checkPass
                            }), {
                                headers: {}
                            }).then((res) => {
                                console.log(res)
                                this.$router.replace({
                                    path: '/login'
                                })
                                window.localStorage.clear();
                    			window.sessionStorage.clear();
                            })
                        }
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '修改失败'
                        });
                    })
                } else {
                    console.log('error submit!!');
                    return false;
                }
            })
            /*var _this = this
            this.$confirm('确认修改密码, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then((action) => {
                if (action === 'confirm') {
                    _this.$axios.post('/tomlive/user/updateUserPassword', this.$qs.stringify({
                        userName: _this.form.userName,
                        password: _this.form.checkPass
                    }), {
                        headers: {}
                    }).then((res) => {
                        console.log(res)
                        this.$router.replace({
                            path: '/home/sub_password'
                        })
                    })
                }
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '修改失败'
                });
            })*/
        }
    },
    mounted() {
        var userName = window.sessionStorage.getItem('userName')
        this.form.userName = userName
    }
}
</script>
<style scoped>
.modify_pw {
    width: 100%;
    min-height: 800px;
    margin: 20px 0 0 0;
    background: #fff;
    border: 1px solid #e9e9e9;
    box-sizing: border-box;
}

.modify_title {
    width: 100%;
    height: 50px;
    border-bottom: 1px solid #e9e9e9;
    font-size: 14px;
    line-height: 50px;
    color: #666;
    font-weight: bold;
    box-sizing: border-box;
}

.modify_title>span {
    margin: 0 20px 0 20px;
}

.ads {
    width: 100%;
    height: 40px;
    margin: 20px 0 0 0;
}

.as {
    margin: 90px 0 0 0;
}

.subs {
    width: 100%;
    height: 40px;
    margin: 178px 0 0 0;
}

.subs>.el-row>.el-col>button {
    padding: 12px 30px;
}

.r {
    float: right;
}

.r>.rs {
    color: red;
    margin: 0px 5px;
}
</style>